#InsuranceProduct {
  position: relative;
  border: 1px solid #dedede;
  width: 100%;
  height: 600px;
  background: #00BCD4;
  color: #fff;

  li {
    list-style: none;
  }

  .icon-left-arrow, .icon-right-arrow {
    position: absolute;
    top: 233px;
  }

  .icon-left-arrow {

    left: 27px;
  }

  .icon-right-arrow {

    right: 27px;
  }

  .active {
    color: #C42323;
  }

  .ip-con {
    display: inline-block;
    margin: 35px;
    float: left;

    strong {
      float: left;
      font-size: 16px;

      i {
        margin-right: 5px;
      }
    }

    .ip-right {
      float: left;
      margin-left: 20px;
      color: #666666;

      p {
        margin-bottom: 20px;

        span {
          float: left;
          color: #333333;
          font-weight: 600;
        }

        li {
          margin-left: 56px;
        }
      }
    }

  }
}
