.orderDetail {
  width: 100%;
  background: #fff;
  box-shadow: 0 8px 16px 0 rgba(30,56,90,0.05);
  border-radius: 4px;
  padding: 20px 0 0 20px;

  h2 {
    color: #1caa20;
  }

  h3 {
    color: #4c4c4c;
  }


  .od-modul1, .od-modul2 {
    width: 90%;

  }

  .od-modul1 {
    height: 300px;
    .od-tick {
      padding: 20px;

      .od-tick-left {
        float: left;
        width: 50%;
        border-right: 1px dashed #dedede;
      }

      .od-tick-right {
        float: right;
        width: 40%;

        .otr-price {
          margin-left: 5px;
          font-size: 30px;
          color: #aa150f;
        }
      }
    }
  }

  .od-modul2 {
    padding: 20px;
    height: 340px;
    background: #dedede;
    .od-mod-flow:nth-of-type(1){
      border-bottom: 1px dashed #dedede;
    }
    .od-mod-flow {
      float: left;
      padding: 0 20px;
      width: 97%;
      height: 120px;
      background: #fff;
      .flow-left{
        float: left;
        width: 40%;
      }
      .flow-center{
        width: 18%;
        float: left;
      }
      .flow-right{
        float: right;
        width: 30%;
        .fl-rg-span{
          margin-right: 20px;
        }
      }
    }
  }
  .od-butt{
    position: absolute;
    right: 157px;
    margin: 20px;
    .od-but{
      float: left;
      margin-right: 5px;
      border-radius: 8px;
      border: 1px solid #aa150f;
      width: 160px;
      height: 50px;
      background: #fff;
      color: #aa150f;
    }
  }

}
.frame{
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1011;
  padding: 20px;
  width: 150px;
  height: 100px;
  margin: auto;
  background: #FFFFFF;
  border-radius: 4px;
  text-align: center;
.fr-but{
  background: #790;
  color: #fff;
  border-radius: 4px;
  border: none;
  padding: 5px 10px;
}
}
.frame-backMeak{
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, .6);
  height: 100%;
  z-index: 1010;
}
