#InsuranceProduct {
  position: relative;
  border: 1px solid #dedede;
  width: 100%;
  height: 600px;
  background: #00BCD4;
  color: #fff;
}
#InsuranceProduct li {
  list-style: none;
}
#InsuranceProduct .icon-left-arrow,
#InsuranceProduct .icon-right-arrow {
  position: absolute;
  top: 233px;
}
#InsuranceProduct .icon-left-arrow {
  left: 27px;
}
#InsuranceProduct .icon-right-arrow {
  right: 27px;
}
#InsuranceProduct .active {
  color: #C42323;
}
#InsuranceProduct .ip-con {
  display: inline-block;
  margin: 35px;
  float: left;
}
#InsuranceProduct .ip-con strong {
  float: left;
  font-size: 16px;
}
#InsuranceProduct .ip-con strong i {
  margin-right: 5px;
}
#InsuranceProduct .ip-con .ip-right {
  float: left;
  margin-left: 20px;
  color: #666666;
}
#InsuranceProduct .ip-con .ip-right p {
  margin-bottom: 20px;
}
#InsuranceProduct .ip-con .ip-right p span {
  float: left;
  color: #333333;
  font-weight: 600;
}
#InsuranceProduct .ip-con .ip-right p li {
  margin-left: 56px;
}
.orderDetail {
  width: 100%;
  background: #fff;
  box-shadow: 0 8px 16px 0 rgba(30, 56, 90, 0.05);
  border-radius: 4px;
  padding: 20px 0 0 20px;
}
.orderDetail h2 {
  color: #1caa20;
}
.orderDetail h3 {
  color: #4c4c4c;
}
.orderDetail .od-modul1,
.orderDetail .od-modul2 {
  width: 90%;
}
.orderDetail .od-modul1 {
  height: 300px;
}
.orderDetail .od-modul1 .od-tick {
  padding: 20px;
}
.orderDetail .od-modul1 .od-tick .od-tick-left {
  float: left;
  width: 50%;
  border-right: 1px dashed #dedede;
}
.orderDetail .od-modul1 .od-tick .od-tick-right {
  float: right;
  width: 40%;
}
.orderDetail .od-modul1 .od-tick .od-tick-right .otr-price {
  margin-left: 5px;
  font-size: 30px;
  color: #aa150f;
}
.orderDetail .od-modul2 {
  padding: 20px;
  height: 340px;
  background: #dedede;
}
.orderDetail .od-modul2 .od-mod-flow:nth-of-type(1) {
  border-bottom: 1px dashed #dedede;
}
.orderDetail .od-modul2 .od-mod-flow {
  float: left;
  padding: 0 20px;
  width: 97%;
  height: 120px;
  background: #fff;
}
.orderDetail .od-modul2 .od-mod-flow .flow-left {
  float: left;
  width: 40%;
}
.orderDetail .od-modul2 .od-mod-flow .flow-center {
  width: 18%;
  float: left;
}
.orderDetail .od-modul2 .od-mod-flow .flow-right {
  float: right;
  width: 30%;
}
.orderDetail .od-modul2 .od-mod-flow .flow-right .fl-rg-span {
  margin-right: 20px;
}
.orderDetail .od-butt {
  position: absolute;
  right: 157px;
  margin: 20px;
}
.orderDetail .od-butt .od-but {
  float: left;
  margin-right: 5px;
  border-radius: 8px;
  border: 1px solid #aa150f;
  width: 160px;
  height: 50px;
  background: #fff;
  color: #aa150f;
}
.frame {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1011;
  padding: 20px;
  width: 150px;
  height: 100px;
  margin: auto;
  background: #FFFFFF;
  border-radius: 4px;
  text-align: center;
}
.frame .fr-but {
  background: #790;
  color: #fff;
  border-radius: 4px;
  border: none;
  padding: 5px 10px;
}
.frame-backMeak {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.6);
  height: 100%;
  z-index: 1010;
}
i {
  display: inline-block;
  width: 18px;
  height: 18px;
  background: #FF5722;
  color: #fff;
  font-style: normal;
  margin: 0 10px;
}
ul,
li {
  list-style: none;
}
a {
  text-decoration: none;
}
