.io-list {
  float: left;
  width: 10%;
  margin-right: 20px;
}
.io-list li {
  background: #4CAF50;
  text-align: center;
  margin-bottom: 5px;
  padding: 5px 0;
}
.io-list li a {
  color: #fff;
}
.io-con {
  float: left;
  width: 88%;
}
.insuranceOrder .io-centerCon {
  width: 1200px;
  margin: auto;
}
